import React from "react";
import { SubscriptionService } from '../../services/SubscriptionService';
import { HTMLContent } from "../Content/Content";

class SubscriberContent extends React.Component<{content: string}>
{
  render() {
    return (
      <div className="subscriber-content-container">
        <HTMLContent content={this.props.content}></HTMLContent>
      </div>
    );
  }
}

